import Vue from 'vue'
import Router from 'vue-router'
// 五个导航首位
import Home from '../view/Home'
// import Home from "../view/Home"
import KeCheng from '../view/KeCheng';
import LianXi from '../view/LianXi';
import Me from '../view/User/Me';
import YueKe from '../view/YueKe';
import Index from '../view/Index'


  

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      redirect:'/home'
    },
    {
      path: '/home',
      name:'Home',
      component:Home,
      redirect:'/home/index',
      children:[
        {
          path:'index',
          name:'Index',
          component:Index
        },
        {
          path:'lianxi',
          name:'LianXi',
          component:LianXi
        },
        {
          path:'kecheng',
          name:'KeCheng',
          component:KeCheng
        },
        {
          path:'me',
          name:'Me',
          component:Me,
          beforeEnter:((to,from,next)=>{
            if(localStorage.getItem('L1uEanLogin')){
              next()
            }else{
              next('/login')
            }
          })
          
        },
        {
          path:'YueKe',
          name:'YueKe',
          component:YueKe
        },
      ]
    },
    //User. 登陆注册界面
    {
      path:'/login',
      name:'Login',
      // 路由懒加载 当点击的时候才会调用
      component:() => import('../view/User/Login')
    },
    // User.设置密码页面
    {
      path:'/passworld',
      name:'PassWorld',
      component:() => import('../view/User/PassWorld')
    },
    // User.个人中心页面
    {
      path:'/person',
      name:'PerSon',
      component:()=> import ('../view/User/PerSon')
    }
    
  ]
})
